@import url(https://fonts.googleapis.com/css?family=Monoton|Sen:400,700&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html{
  font-size: 62.5%;
}

body {
  font-family: 'Sen', sans-serif;
}

a{
  text-decoration: none;
}


.Card{height:28vh;width:23vw;display:flex;flex-direction:column;align-items:center;justify-content:space-between;background-color:#0C0F0A;overflow-x:hidden;margin-bottom:2rem;cursor:pointer}.Card .Card--Img{height:20vh;width:auto;border-radius:0.5rem}.Card .Card--Content{height:8vh;width:23vw;display:flex;align-items:center;justify-content:center}.Card .Card--Content .Card--Title{font-size:2.5rem;color:#FE4A49;font-weight:400;text-align:center}@media (max-width: 425px){.Card{height:38vh;width:90vw}.Card .Card--Img{height:30vh}.Card .Card--Content{height:10vh;width:90vw}.Card .Card--Content .Card--Title{font-size:2rem}}

@font-face{font-family:'Impact';src:url(/static/media/Impact.e442d803.woff2) format("woff2"),url(/static/media/Impact.cac43ed5.woff) format("woff");font-weight:normal;font-style:normal}.Writer{z-index:10;width:auto;height:auto;font-family:'Impact';border:none;background-color:transparent;color:#0C0F0A;outline:none;font-size:2.5rem}

.Input{width:20vw;height:4vh;font-family:'Sen', sans-serif;border:none;background-color:transparent;border-bottom:1px solid #fff;color:#fff;outline:none;padding-left:1rem;font-size:1.5rem;margin-bottom:3rem}.Input::-webkit-input-placeholder{font-size:1.5rem;color:#fff}.Input:-ms-input-placeholder{font-size:1.5rem;color:#fff}.Input::-ms-input-placeholder{font-size:1.5rem;color:#fff}.Input::placeholder{font-size:1.5rem;color:#fff}.Input:placeholder-shown{padding-left:1rem}@media (max-width: 425px){.Input{width:60vw;margin-bottom:0}}

.Button{text-decoration:none;padding:1rem 2rem;background-color:transparent;font-size:1.7rem;font-family:'Sen', sans-serif;border-radius:1rem;border:1px solid #E5FFDE;color:#E5FFDE;font-weight:400;cursor:pointer;text-transform:uppercase}.Button:hover{background-color:#99E1D9;border-color:#99E1D9;color:#0C0F0A}.Button:focus{outline:none}@media (max-width: 425px){.Button{font-size:1.4rem}}

.Editor{min-height:60vh;width:80vw;margin-left:10vw;margin-right:10vw;display:flex;justify-content:space-around;align-items:center;margin-bottom:5vh}.Editor .Editor--ImgContainer{height:60vh;width:auto;position:relative;overflow:hidden}.Editor .Editor--ImgContainer .Editor--Img{height:60vh;width:auto}.Editor .Editor--ImgContainer .Editor--ImgInsertText{height:100%;width:100%;position:absolute;top:0;left:0;z-index:5;overflow:hidden}.Editor .Editor--ImgContainer .Editor--ImgInsertText .Editor--ImgInsertTextContainer{height:100%;width:100%;position:relative;overflow:hidden}.Editor .Editor--Content{height:40vh;width:30vw;display:flex;flex-direction:column;align-items:center;justify-content:space-around}.Editor .Editor--Content .Editor--ContentHeadline{font-size:3.5rem;font-weight:400;color:#E5FFDE;text-transform:capitalize}@media (max-width: 425px){.Editor{min-height:70vh;width:90vw;margin-left:0;margin-bottom:0;margin-right:0;flex-wrap:wrap;margin-top:2rem;margin-bottom:2rem}.Editor .Editor--ImgContainer{height:auto;width:90vw}.Editor .Editor--ImgContainer .Editor--Img{height:auto;width:90vw}.Editor .Editor--Content{height:35vh;width:70vw;justify-content:space-between;margin-top:2rem}.Editor .Editor--Content .Editor--ContentHeadline{font-size:2.5rem;text-align:center}}

.Memepage{width:99vw;background-color:#0C0F0A;display:flex;align-items:center;justify-content:space-around;flex-wrap:wrap;padding-top:16vh}.Memepage .Memepage--Search{height:10vh;width:100vw;display:flex;align-items:center;justify-content:space-around;margin-bottom:5vh}.Memepage .Memepage--Search .Memepage--SearchHeading{font-size:4rem;font-weight:400;color:#E5FFDE}.Memepage .Memepage--Search .Memepage--SearchBar{width:15vw;height:5vh;font-family:'Sen', sans-serif;background-color:transparent;border:1px solid #fff;color:#fff;outline:none;padding-left:1rem;font-size:2rem;border-radius:0.5rem}.Memepage .Memepage--Search .Memepage--SearchBar::-webkit-input-placeholder{font-size:2rem;color:#fff}.Memepage .Memepage--Search .Memepage--SearchBar:-ms-input-placeholder{font-size:2rem;color:#fff}.Memepage .Memepage--Search .Memepage--SearchBar::-ms-input-placeholder{font-size:2rem;color:#fff}.Memepage .Memepage--Search .Memepage--SearchBar::placeholder{font-size:2rem;color:#fff}.Memepage .Memepage--Search .Memepage--SearchBar:placeholder-shown{padding-left:1rem}@media (max-width: 425px){.Memepage{padding-top:13vh}.Memepage .Memepage--Search{height:20vh;margin-bottom:0;flex-wrap:wrap}.Memepage .Memepage--Search .Memepage--SearchHeading{font-size:2.5rem;text-align:center;margin-top:1rem}.Memepage .Memepage--Search .Memepage--SearchBar{width:65vw;margin-bottom:2rem;margin-top:2rem}}

.Navbar{height:12vh;width:100vw;background-color:#0C0F0A;position:fixed;top:0;left:0;border-bottom:1px solid #E5FFDE;display:flex;align-items:center;justify-content:space-between;z-index:100}.Navbar .Navbar--Title{font-size:5rem;font-family:'Monoton', cursive;font-weight:400;color:#E5FFDE;margin-left:5vw}.Navbar .Navbar--Links{height:100%;width:15%;display:flex;align-items:center;justify-content:space-around}.Navbar .Navbar--Links .Navbar--Links--Link:focus{outline:none}.Navbar .Navbar--Links .Navbar--Links--Link::-webkit-file-upload-button{visibility:hidden}.Navbar .Navbar--Links .Navbar--Links--Link::before{content:'Upload';display:inline-block;font-family:'Monoton', cursive;color:#E5FFDE;border:1px solid #E5FFDE;border-radius:0.5rem;padding:1rem 2rem;outline:none;-webkit-user-select:none;cursor:pointer;font-size:2.7rem;margin-top:2rem;text-transform:uppercase}.Navbar .Navbar--Links .Navbar--Links--Link:hover::before{border-color:#FE4A49;background-color:#FE4A49}@media (max-width: 425px){.Navbar .Navbar--Title{font-size:3rem;margin-left:4vw}.Navbar .Navbar--Links{width:30%}.Navbar .Navbar--Links .Navbar--Links--Link::before{font-size:1.7rem;margin-top:0;margin-left:5rem;font-family:'Sen', sans-serif}}

.App{
    background-color: #0C0F0A;
    min-height: 100vh;
}
